'use strict';

export enum FileDialogMode {
    open = 0,
    save = 1
}

export interface FileDialogOptions {
    // The default name of the file to open or save.
    name?: string;

    // A string array of extensions to populate in the dialog.
    // File extension format "Text Files (*.txt)|*.txt" where the left side of the pipe is
    // the string to be displayed and the right side is the extension to match.
    extensions?: string[];

    // The index of the default extension to highlight in the list.
    extensionsIndex?: number;

    // The initial directory of the file dialog.  Relative paths are relative to the current persistence location.
    initialDirectory?: string;

    // The title of the file dialog.
    title?: string;
}